<template>
	<view>
		<!-- 商家介绍 -->
		<Business :busidata='busidata'></Business>
		<!-- tab切换 -->
		<!-- 接受子组件的自定义事件 -->
		<Tabs @change='getNum'></Tabs>
		<view class="ordering-sceate">
		<!-- 商品下单 -->
		<Ordering :class="[hideing === 0 ? 'actineclass' : 'errorclass']"
		:orderingdata='orderingdata'
		:busidata='busidata'
		></Ordering>
		<!-- 评价 -->
		<Message 
		:messagedata='messagedata'
		:tabmessage='tabmessage'
		:empty='empty'
		:class="[hideing === 1 ? 'actineclass' : 'errorclass']"
		></Message>
		<!-- 商家详细 -->
		<Introduce 
		:busidata='busidata'
		
		:class="[hideing === 2 ? 'actineclass' : 'errorclass']"
		></Introduce>
		</view>
	</view>
</template>

<script>
	//引入组件
	import Business from '../../components/Business.vue'
	import Tabs from '../../components/Tabs.vue'
	import Ordering from '../../components/Ordering.vue'
	import Message from '../../components/Message.vue'
	import Introduce from '../../components/Introduce.vue'
	// POST请求
	import {
		publicing
	} from '../../api/api.js'
	// 请求地址
	import {
		discussurl,
		shopurl,
		getdishesurl
	} from '../../api/request.js'


	export default {
		components: {
			Business,
			Tabs,
			Ordering,
			Message,
			Introduce,
		},
		data() {
			return {
				hideing: 0,
				//商家介绍
				busidata:{},
				//商品数据
				orderingdata:[],
				//评论
				messagedata:[],
				//评论分类标签
				tabmessage:'',
				//标识没有评论
				empty:'',
				// 商家唯一标识
				ids:''
			}
		},
		methods: {
			getNum(event){
				this.hideing=event
			},
			//请求接口
			takeFun(ids){
				let data ={
					openid:ids
				}
				let disdata={
					merchantid:ids
				}
				Promise.all([publicing(shopurl,data),publicing(getdishesurl,data),publicing(discussurl,disdata)])
				.then((res)=>{
					// console.log(res);
					//商家介绍
					this.busidata = res[0].data[0]
					//商品数据
					this.orderingdata=res[1].data
					//评论
					this.messagedata=res[2].data
					if(res[2].data.length === 0){
						this.empty = 'EMPTY'
						
					}else{
						//放在这里能防止 点击对应评价 不消失 所有标签
						// 评论分类标签去重复和去空标签
						// 取出评论分类标签放在一个数组里
					let messageword = res[2].data.map((item)=>{
						return item.classmessage
					})
						// 标签去重
					let newarr = Array.from(new Set(messageword))
						// 标签去空值
					var newArr = newarr.filter(item => item)
						// 数组合并
					this.tabmessage = ['全部', ...newArr]
					}
				})
				.catch((err)=>{
					// console.log(err);
				})
			},
			// 被评论子组件调用的函数
			messageMethod(item){
				if(item==='全部'){
					let data ={
						merchantid:this.ids
					}
					this.classifMess(data)
				}else{
					let data={
						merchantid:this.ids,
						classmessage:item
					}
					this.classifMess(data)
				}
			},
			//请求评论分类的数据
			classifMess(data){
				publicing(discussurl,data)
				.then((res)=>{
					this.messagedata = res.data
				})
				.catch((err)=>{
					console.log(err);
				})
			}
		},
		onLoad(e) {
			this.ids = e.id
			this.takeFun(e.id)
		}
	}
</script>

<style scoped>
	.actineclass {
		display: block;
		overflow-y: auto;
		height: 100%;
	}

	.errorclass {
		display: none;
	}

	.ordering-sceate {
		position: fixed;
		left: 0;
		right: 0;
		bottom: 0;
		top: 230rpx;
	}
</style>
